.page {
  color: white;
  font-family: "Noto Sans", sans-serif;
}

/* NavBar ------------------------------------------------------ */

.navbar {
  display: flex;
  background-color: black;
  color: white;
  position: relative;
  z-index: 5;
}

.linkContainer {
  display: flex;
  margin-left: 30px;
}

.link {
  margin-left: 150px;
}

.contactLink {
  margin-left: 920px;
}

.effect {
  color: white;
  position: relative;
  text-decoration: none;
  transition: color 0.4s ease-out;
}

.effect::after {
  border-radius: 50%;
  border-top: 3px solid #ff8c00;
  content: "";
  position: absolute;
  right: 100%;
  bottom: -5px;
  left: 0;
  transition: right 0.4s cubic-bezier(0, 0.5, 0, 1), border-color 0.4s ease-out;
}

.effect:hover {
  text-decoration: none;
  color: #ff8c00;
}

.effect:hover::after {
  right: 0;
  color: white;
}

/* Homepage ----------------------------------------------------- */

.introCont {
  display: flex;
  flex-direction: row;
  margin-left: 180px;
  margin-top: 200px;
  line-height: 1px;
}

.hi {
  font-size: 30px;
}

.position {
