  color:#ff8c00;
}

.facts {
  font-size: 20px;
  color: rgb(214, 211, 211);
}

#profile-pic {
  width: 400px;
  margin-top: 130px;
}

#profile-pic:hover {
  transition: 1s;
  -webkit-transition: 1s;
  -webkit-filter: grayscale(100%);
